import React from 'react';
import propTypes from 'prop-types';

import styles from './index.module.scss';

class Showcase extends React.Component {
    state = {
        current: 1,
        show: false,
        showCloseIcon: false,
    }
    next = () => {
        this.setState((state, props) => {
            let current = state.current + 1;
            if (current < props.images.length) {
                return { current };
            } else {
                return state;
            }
        });
    }
    previous = () => {
        this.setState((state, props) => {
            let current = state.current - 1;
            if (current >= 0) {
                return { current };
            } else {
                return state;
            }
        });
    }
    show = (index) => {
        if (typeof (index) === 'number' && index >= 0 && index < this.props.images.length) {
            this.setState({ current: index });
        }
        this.setState({ show: true });
    }
    hide = () => {
        this.setState({ show: false, showCloseIcon: false });
    }
    handleClickImage = (e) => {
        e.stopPropagation();
        this.setState({ showCloseIcon: true });
    }
    render() {
        let display;
        if (this.state.show) {
            display = <div className={styles.Showcase}>
                <div className={styles.previous} onClick={this.previous}><p>{'<'}</p></div>
                <div className={styles.container} onClick={this.hide}>
                    <img src={this.props.images[this.state.current].origin} alt="origin" onClick={this.handleClickImage} />
                    <div className={styles.closeIcon + (this.state.showCloseIcon ? ' ' + styles.closeIconShow : '')} ><span>X</span></div>
                </div>
                <div className={styles.next} onClick={this.next}><p>{'>'}</p></div>
            </div>
        } else {
            display = null;
        }
        return <React.Fragment>
            {display}
            <ImagePreLoad images={this.props.images} />
        </React.Fragment>
    }
}

Showcase.propTypes = {
    images: propTypes.array.isRequired,
}

class ImagePreLoad extends React.Component {
    state = {
        timeout: false,
    }
    componentDidMount() {
        this.h = setTimeout(() => {
            this.setState({timeout: true});
        }, 1000);
    }
    componentWillUnmount() {
        clearTimeout(this.h);
    }
    render() {
        if (this.state.timeout) {
            return <div className={styles.ImagePreLoad}>
                {this.props.images.map(img => <img src={img.origin} alt="pre-load" />)}
            </div>
        } else {
            return null;
        }
    }
}

export default Showcase;
