import React from 'react';
import propTypes from 'prop-types';
import { withRouter } from 'react-router-dom';

import Showcase from '../Showcase';

import styles from './index.module.scss';

class ThumbnailWrapped extends React.Component {
    render() {
        return <div className={styles.Thumbnail} onClick={this.props.onClick}><div>
            <img src={this.props.src} />
        </div></div>
    }
}
var Thumbnail = withRouter(ThumbnailWrapped);

class Templete extends React.Component {
    constructor(props) {
        super(props);
        this.showcaseRef = React.createRef();
    }
    handleThumbnailClick = (index) => {
        this.showcaseRef.current.show(index);
    }
    render() {
        return <React.Fragment>
            <div className={styles.Templete}>
                <div className={styles.left}>
                    <img src={this.props.leftImage} alt="left" />
                </div>
                <div className={styles.right}>
                    <div className={styles.container}>
                        <div className={styles.thumbnailContainer}>
                            {this.props.images.map((img, index) => <Thumbnail key={index} src={img.thumbnail} onClick={() => this.handleThumbnailClick(index)} />)}
                        </div>
                    </div>
                </div>
            </div>
            <Showcase images={this.props.images} ref={this.showcaseRef} />
        </React.Fragment>
    }
}

// Templete.propTypes = {
//     leftImage: propTypes.object.isRequired,
//     images: propTypes.array.isRequired,
// }

export default Templete;
