import React from 'react';

import styles from './index.module.scss';

import Templete from '../Templete';

import leftText from './left-text.jpg';

import img1 from './t1.jpg';
import img2 from './t2.jpg';
import img3 from './t3.jpg';
import img4 from './t4.jpg';
import img5 from './t5.jpg';
import img1Origin from './t1-origin.jpg';
import img2Origin from './t2-origin.jpg';
import img3Origin from './t3-origin.jpg';
import img4Origin from './t4-origin.jpg';
import img5Origin from './t5-origin.jpg';

class Coco extends React.Component {
    images = [{
        thumbnail: null,
        origin: null,
    }, {
        thumbnail: img1, 
        origin: img1Origin,
    }, {
        thumbnail: img2, 
        origin: img2Origin,
    }, {
        thumbnail: img3, 
        origin: img3Origin,
    }, {
        thumbnail: img4, 
        origin: img4Origin,
    }, {
        thumbnail: img5, 
        origin: img5Origin,
    }];
    render() {
        return <Templete leftImage={leftText} images={this.images} />
    }
}

export default Coco;
