import React from 'react';
import { withRouter } from 'react-router-dom';

import styles from './index.module.scss';

import icon1 from './1.jpg';
import icon2 from './2.jpg';
import icon3 from './3.jpg';
import icon4 from './4.jpg';
import iconBottom from './icon.jpg';

function IconWTitle(props) {
    var img = <img src={props.src} alt="img1" />;
    var title = <p>{props.title}</p>
    return <div className={[styles.IconWTitle, props.className].join(' ')} onClick={() => props.history.push(props.to)}>
        {props.under === true ? [img, title] : [title, img]}
    </div>
}
var WrIconWTitle = withRouter(IconWTitle);

class Design extends React.Component {
    render() {
        return <div className={styles.Design}>
            <div className={styles.container}>
                <h1>DESIGN</h1>
                <div className={styles.iconContainer}>
                    <WrIconWTitle src={icon1} className={styles.IconWTitle1} title="BOND" to="/design/bond" />
                    <WrIconWTitle src={icon2} className={styles.IconWTitle2} title="BLOOM" to="/design/bloom" /><br />
                    <WrIconWTitle src={icon3} className={styles.IconWTitle3} title="HOLE" to="/design/hole" under={true} />
                    <WrIconWTitle src={icon4} className={styles.IconWTitle4} title="COCO" to="/design/coco" under={true} />
                </div>
                <img src={iconBottom} alt="iconBottom" className={styles.iconBottom}/>
            </div>
        </div>
    }
}

export default Design;