import React from 'react';
import { NavLink, withRouter } from 'react-router-dom';

import styles from './index.module.scss';

import icon from './icon.svg';
import pan from './dish.png';

function IconWTitle(props) {
    return <div className={styles.IconWTitle} onClick={() => props.history.push(props.to)}>
        <img src={icon} alt="icon-top" className={props.className} />
        <div>{props.title}</div>
    </div>
}

var WrIconWTitle = withRouter(IconWTitle);

class Portfolio extends React.Component {
    render() {
        return <div className={styles.Portfolio}>
            <img className={styles.pan} src={pan} alt="pan" />
            <div className={styles.container}>
                <h1>PORTFOLIO</h1>
                <h2>YANING YUAN</h2>
                <h3>2012-2016 BECHALOR DEGREE:PHYSICS<br />
                    2016-2019 MASTER DEGREE: ARCHITECTURE</h3>

                <h4>BREAKING SUBJECT BOUNDARIES</h4>
                <p>
                    <span>
                        I think architects are a group of people who should know more than any subject.
                        They should havecertain computer programming skills, a strong mathematical foundation, and the most basic aesthetic.
                        However, these abilities that should have been possessed have been cut by domestic cramming education
                            until only aesthetics is left.
                        Architecture is not pure art. I don't know when it started, design has standardlized evalution criteria.
                        The result is a standardized design that limits the architect's version.
                    </span></p>
                <h4>I HOPE I CAN BE AN IRREPLACEABLE PERSON</h4>
                <div className={styles.iconContainer}>
                    <WrIconWTitle title="TIMELINE" className={null} to="/timeline" />
                    <WrIconWTitle title="DESIGN" className={styles["img-rotate-90deg"]} to="/design" />
                    <WrIconWTitle title="RESEARCH" className={styles["img-rotate-180deg"]} to="/research" />
                    <WrIconWTitle title="OTHER" className={styles["img-rotate-270deg"]} to="/other" />
                </div>
            </div>
        </div>
    }
}

export default Portfolio;